/* stylelint-disable */
@mixin for-desktop {
  @media (min-width: 769px) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: (888px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (575px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (400px)) {
    @content;
  }
}
